@charset "UTF-8";
.ql-container.ql-snow {
  font-family: "Roboto";
  font-size: .8em;
  border: 1px solid #d9d9d9 !important;
  border-radius: 0.2em;
  background: #fff; }
  .ql-container.ql-snow .ql-editor {
    min-height: 25vh; }

.ql-toolbar.ql-snow {
  font-family: "Roboto";
  position: sticky;
  top: 4em;
  border: none;
  box-shadow: 0px 1px 0px #d9d9d9;
  z-index: 1000;
  background: #f9f9f9; }

.ql-editor ul.task-list > li {
  pointer-events: none; }

.ql-editor ul.task-list > li::before {
  color: #000;
  border: 1px solid #000;
  content: " ";
  font-size: 15px;
  height: 15px;
  line-height: 1;
  position: absolute;
  text-align: center;
  vertical-align: middle;
  width: 15px;
  cursor: default;
  pointer-events: all; }

.ql-editor ul.task-list > li.checked {
  text-decoration: line-through; }

.ql-editor ul.task-list > li.checked::before {
  content: "✔"; }
