nav .burger ~ .menu > * {
  width: 100%; }

nav .burger {
  -webkit-tap-highlight-color: transparent; }

nav .burger ~ .menu > *.icon, nav .burger ~ .menu > *.icon-big {
  width: auto; }

nav {
  text-align: center !important;
  background: #008bce !important; }

.menu {
  padding-top: 1.8em; }
  @media (min-width: 900px) {
    .menu {
      padding-top: 0; } }

.search {
  display: inline-block;
  width: 65%;
  max-width: 400px;
  font-size: 0.9em; }
  @media (min-width: 900px) {
    .search {
      position: absolute !important;
      left: calc(50% - (400px / 2)); } }
  .search .search-input {
    padding-right: 1.8em;
    border: none; }
  .search .search-clear {
    position: absolute;
    right: 0;
    margin: 0;
    background: transparent;
    color: #111; }
    .search .search-clear:hover {
      background: transparent; }

.close-hamburger {
  position: absolute;
  top: 0.6em;
  right: 0.6em;
  display: inline; }
